main{
    display: flex;
    justify-content: center;
    font-family: sans-serif;
    font-size: 1.5rem;
    flex-direction: column;
    align-items: center;
    
}


h1{
    margin-left: 25%;
}

input{
    width: 50vw;
    padding-top: 2px;
    padding-bottom: 2px;
    box-shadow: 0px  2px  #888888;
    font-size: 1rem;
    border: 0px;
}


input::placeholder{
    font-size: 0.95rem;
    
}

button#a{
    margin-left: -25px;
    background-color: white;
    opacity: 0.4;
    border: 0px;
}

#a:hover{
    opacity: 0.8;
    background-color: red;
}

#b{
    cursor: pointer;
    opacity: 1;
    margin-left: 5px;
    padding: 0.39rem 15px;
    color: white;
    border: 0px;
    background-color: darkcyan;
}

#b:hover{
    opacity: 0.7;
    
    
}

p#intro{
    display: flex;
    align-self: center;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    color: black;
    width: 50%;
    text-align: center;
    margin: 5% 22.5%;

}